/* タブ（リンク）のスタイリング */
.nav-link {
  border: 1px solid #ccc;
  border-right: none; /* 右の境界線を削除 */
  padding: 10px;
  text-decoration: none;
  color: black;
}


/* 余分なマージン、パディング、リストスタイル（点）を削除 */
ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

/* 最後のタブだけは右の境界線を追加 */
.nav-link:last-child {
  border-right: 1px solid #ccc;
}

.nav-link:hover {
  border-color: #007bff;
  background-color: #c1af9a;
}

.contact-container {
  padding: 20px;
  border: 1px solid #ccc;
}

.contact-heading {
  font-size: 24px;
  margin-bottom: 20px;
}

.contact-link {
  display: block;
  margin-bottom: 10px;
  color: #007bff;
  text-decoration: none;
}

.contact-link:hover {
  text-decoration: underline;
}

header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white; /* 好みの色に設定できます */
  z-index: 1000; /* 他の要素の上に表示されるようにするため */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* 視認性を高めるためにオプションで追加 */
}

body {
  padding-top: 120px; /* ヘッダーの高さに応じてこの値を調整してください */
}

footer {
  text-align: center;
  padding: 20px;
  background-color: #f2f2f2; /* 任意の背景色 */
}

.thinking-text {
  font-size: 16px;
  color: #333; /* 任意の文字色 */
}

.dots {
  display: inline-block;
  overflow: hidden;
  vertical-align: bottom;
}

.dots::after {
  display: inline-block;
  content: "...";
  animation: dotAnimation 1.5s steps(3, end) infinite;
  vertical-align: bottom;
}

@keyframes dotAnimation {
  0%, 20% {
    content: ".";
  }
  40% {
    content: "..";
  }
  60% {
    content: "...";
  }
  80%, 100% {
    content: "";
  }
}